/* ##########################
Header
########################## */
header{
    background-position: center;
    background-size: cover;
    background-image: url('https://sourabhportfolio.vercel.app/_next/image?url=%2F_next%2Fstatic%2Fmedia%2Fherobg.c94a10be.jpg&w=3840&q=75');
}
header nav{
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5%;
}
header nav .left{}
header nav .right{}
header nav .right ul{
    list-style-type: none;
}
header nav .right ul li{
    display: inline;
    margin-left: 24px;
    font-weight: 500;
    color: rgba(216, 219, 255, 0.6);
}
header nav .right ul li a{
    color: rgba(255, 255, 255, 0.5);
    text-decoration: none;
    transition: 0.3s ease-in-out;
}
header nav .right ul li a:hover{
    color: white;
}
/* Hero  */
#hero{
    height: calc(100vh - 70px);
    /* display: flex;
    align-items: center; */
    }
#hero h1{
    font-size: 5rem;
    font-weight: 900;
    margin-top: 48px;
}
#hero h1 span{
    color: #9129db;
}
#hero p{
    font-size: 2rem;
}
/* ##########################
Main
########################## */
section{}

/* ##########################
Overview
########################## */
.overview{}
.overview > p{
    color: rgba(216, 219, 255, 0.6);
    max-width: 900px;
    margin-bottom: 48px;
}
.overview > p span{
    color: rgba(236, 235, 255, 0.8);
}
.overview .skill-cards{
}
.overview .skill-cards .item{
    display: inline-block;
    margin-right: 16px;
    margin-bottom: 16px;
    padding: 80px 32px;
    width: 280px;
    background-color: rgba(150, 3, 255, 0.05);
    border: 2px solid rgba(255, 255, 255, 0.108);
    text-align: center;
    border-radius: 24px;
}
.overview .skill-cards .item img{
    width: 60px;
    margin-bottom: 32px;
    transition: 0.5s;
}
.overview .skill-cards .item:hover img{
    transform: rotateZ(40deg);
}
.overview .skill-cards .item p{
    font-size: 1.25rem;
    font-weight: 600;
    color: white;
}
@media (max-width:600px){
.overview .skill-cards .item{
    width: 100% !important;
}
}
/* ##########################
Work Experience
########################## */
.work-experience{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-top: 48px;
}
.work-experience .line{
    width: 12px;
    height: 600px;
    background-color: rgba(255, 255, 255, 0.02);
    /* background-color: rgb(11, 10, 12); */
    border: 2px solid #ffffff10;
}
.work-experience .item{
    position: absolute;
    width: 55%;
    max-width: 420px;
    padding: 36px;
    border: 2px solid #9129db80;
    background-color: #080815;
    background-color: #080815;
    /* border: 2px solid rgba(255, 255, 255, 0.108); */
}.work-experience .item h2{}
.work-experience .item p{
    color: rgba(216, 219, 255, 0.6);
}
.work-experience .item .company-name{
    color: #9129db;
    margin-bottom: 16px;
}
.work-experience .corner-1{
    width: 50px;
    height: 50px;
    background-color: #9129db80;
    position: absolute;
    right:calc( 50% + 22px);
    top: 70px;
    transform: rotate(45deg);
}
.work-experience .one{
    right:calc( 50% + 20px);
    top: 48px;
    border-radius: 6px;
}
.work-experience .corner-2{
    width: 50px;
    height: 50px;
    background-color: #9129db80;
    position: absolute;
    left:calc( 50% + 22px);
    bottom: 70px;
    transform: rotate(45deg);

}
.work-experience .two{
    left:calc( 50% + 20px);
    bottom: 48px;
    border-radius: 6px;
}
@media (max-width:600px){
    .work-experience .item{
        width: 100%;
        position: relative;
    }
    .work-experience{
        display: flex;
        justify-content: start;
        flex-direction: column;
        gap: 16px;
    }
    .work-experience .line,.work-experience .corner-1,.work-experience .corner-2{
        display: none;
    }
    .work-experience .one, .work-experience .two{
        position: static;
    }
}
/* ##########################
Skills
########################## */
.skills{
    gap: 16px;
}
.skills .item{
    display: inline-block;
    margin-right: 16px;
    margin-bottom: 16px;
    background-color: rgba(150, 3, 255, 0.05);
    border: 2px solid rgba(255, 255, 255, 0.108);
    text-align: center;
    border-radius: 24px;
    padding: 32px;
    width: 150px;
}
.skills .item img{
    width: 60px;
}
.skills .item img .label{}
@media (max-width:450px){
.skills .item{
    width: calc( 50% - 16px );
    margin-right: 0;
    margin: 8px;
}
}
/* ##########################
Projects
########################## */
.projects{}
.projects > .info{
    max-width: 900px;
    margin-bottom: 48px;
    color: rgba(216, 219, 255, 0.6);
}
.projects .item{
    padding: 16px;
    width: 380px;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 16px;
    display: inline-block;
    margin-right: 16px;
    margin-bottom: 16px;
    border: 1px solid rgba(255, 255, 255, 0.05);
    position: relative;
    transition: 0.3s;
    overflow: hidden;
}
.projects .item .layer{
    width: 100%;
    height: 0;
    position: absolute;
    left: 0;
    top: 0;
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(2px);
    transition: 0.3s;
    z-index: 1;
}
.projects .item .layer a{
    margin-top: -42px;
    color: white;
    background-color: red;
    text-decoration: none;
    padding: 8px 16px;
}
.projects .item:hover > .layer{
    height: 100%;
}
.projects .item img{
    width: 100%;
    border-radius: 8px;
}
.projects .item .title{
    margin-bottom: 8px;
    font-weight: 900;
    font-size: 2rem;
}
.projects .item .info{
    color: rgba(216, 219, 255, 0.6);
    margin-bottom: 8px;
}
.projects .item .labels{
    /* margin-bottom: 16px; */
}
.projects .item .labels span{
    margin-right: 8px;
    opacity: 0.6;
}
.projects .item .labels span:nth-child(1){
    color: rgb(255, 111, 62);
}
.projects .item .labels span:nth-child(2){
    color: rgb(84, 181, 255);
}
.projects .item .labels span:nth-child(3){
    color: rgb(255, 255, 121);
}
.projects .item .labels span:nth-child(4){
    color: rgb(104, 255, 104);
}

@media (max-width:600px){
    .projects .item{
        width: 100% !important;
    }
    }
/* ##########################
Contact
########################## */
.contact-inner{
    display: flex;
    align-items: center;
    justify-content: center;
}
.contact-inner .left{
    flex: 3;
}
.contact-inner .left form{
    /* background-color: red; */
    margin-top: 32px;
}
.contact-inner .left form div{
    display: flex;
    width: 100%;
    gap: 16px;
}
.contact-inner .left form div input, .contact-inner .left form div textarea{
    margin-bottom: 16px;
    width: 100%;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.01);
    border: 1px solid rgba(194, 212, 255, 0.203);
    color: white;
    padding-left: 16px;
}
.contact-inner .left form div textarea{
    height: 180px;
    padding-top: 16px;
}
.contact-inner .left form div button{
    padding: 16px 36px;
    border: transparent;
    width: 100%;
    background-color: #9129db;
    color: white;
    border-radius: 4px;
}
.contact-inner .right{
    flex: 2.5;
}
@media (max-width:800px){
.contact-inner .right{
    flex: 0;
}
}
.alert{
    border-bottom: 6px solid green;
    background-color: red;
    position: fixed;
    /* right: calc(3% + 12px); */
    right: -100%;
    bottom: calc(3% + 12px);
    /* width: fit-content; */
    padding: 12px 24px;
    color: white;
    background-color: rgb(29, 28, 44);
    border-bottom: 6px solid green;
    display: flex;
    align-items: center;
    z-index: 10000;
    transition: 0.5s;
}
.alert img{
    height: 30px;
    margin-right: 12px;
}
